@import "../../../theme/scss/project-var";

.main {
  display: flex;
  justify-content: space-between;
  padding-top: 80px;
  padding-bottom: 20px;

  & > div {
    margin-top: 30px;
  }

  @media screen and (max-width: 1024px) {
    flex-direction: column;
  }
}

.bridge {
  width: 644px;
  height: 410px;

  @media screen and (max-width: 1024px) {
    width: 345px;
    height: auto;
  }
}

.link-wrap {
  display: flex;
  align-items: center;
  width: 100%;
}

.sign-up-for-wautlist-link,
.join-our-crowdloan-link {
  background: $main;
  border-radius: 10px;
  height: 40px;
  width: 174px;
  display: flex;
  align-items: center;
  justify-content: center;

  &:hover {
    text-decoration: none;
  }

  > span {
    font-weight: 500;
    font-size: 16px;
    line-height: 21px;
    color: #ffffff;
  }

  @media screen and (max-width: 1024px) {
    width: 48%;
    margin: 0 auto;
  }
}

.learn-more-link {
  border: solid 1px $main;
  margin-left: 20px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 40px;
  width: 146px;

  &:hover {
    text-decoration: none;
  }

  > span {
    font-weight: 500;
    font-size: 16px;
    line-height: 21px;
    color: $main;
  }

  @media screen and (max-width: 1024px) {
    width: 48%;
    margin-left: 10px;
  }
}

.title-h2 {
  font-family: IBM Plex Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 46px;
  line-height: 60px;

  /* text */

  color: #1e3676;

  span {
    color: $main;
  }

  @media screen and (max-width: 1024px) {
    font-size: 26px;
    line-height: 34px;
    text-align: center;
  }
}

.desc {
  font-weight: normal;
  font-size: 16px;
  line-height: 21px;
  color: #1e3676;
  margin-top: 20px;
  margin-bottom: 30px;

  @media screen and (max-width: 1024px) {
    font-size: 16px;
    line-height: 21px;
    text-align: center;
  }
}

.intro {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  font-family: IBM Plex Sans;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 21px;
  margin-bottom: 40px;

  img {
    margin-right: 20px;
    width: 80px;
    height: 80px;
  }

  > div {
    display: flex;
    align-items: flex-start;
    max-width: 37%;

    &:nth-child(1) {
      padding-right: 0.5em;
    }

    &:nth-child(3) {
      padding-left: 0.5em;
    }
  }

  @media screen and (max-width: 1024px) {
    justify-content: center;
    flex-direction: column;
    text-align: center;
    align-items: center;

    img {
      margin: 0 auto;
    }

    > div {
      flex-direction: column;
      max-width: 100%;
      flex: 1;
      justify-content: center;
    }
  }
}

.intro-kusama {
  background: #fff url(./img/kusama-bg.png) no-repeat 100% / 100%;
  background-position: bottom;
  border-radius: 38px;
  display: flex;
  flex-direction: row-reverse;
  line-height: 21px;
  margin: 10px;
  padding-top: 20px;

  img {
    display: none;
  }

  & > div {
    max-width: 270px;
    padding: 1em 2em;

    &:nth-child(2) {
      order: 2;
    }

    h5 {
      white-space: inherit;
    }
  }

  @media screen and (max-width: 1024px) {
    img {
      display: block;
      width: 100%;
      order: 3;
    }

    justify-content: center;
    flex-direction: column;
    text-align: center;
    flex-direction: column-reverse;
    background-image: none;
    background-color: #f8f8f8;
    margin: 0;
    padding-top: 0;;

    > div {
      max-width: 100%;
      flex-direction: column;
      padding: 1em;
      flex: 1;

      p {
        text-align: left;
      }
    }
  }
}

.main-border {
  margin-bottom: 70px;

  @include main-border();
  @media screen and (max-width: 1024px) {
    border: none;
  }
}

.main-crab-hero {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 36px 0 {
    bottom: 52px;
  }
}

.main-crab-hero-title {
  text-align: center;
  margin-bottom: 8px;
  font-family: IBM Plex Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 46px;
  line-height: 60px;
  text-align: center;
  color: #1E3676;

  @media screen and (max-width: 1024px) {
    font-size: 26px;
    line-height: 34px;
    text-align: center;
  }
}

.crab-hero-desc {
  font-family: IBM Plex Sans;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 26px;
  text-align: center;
  color: #555555;

  @media screen and (max-width: 1024px) {
    font-size: 16px;
    line-height: 21px;
    text-align: center;
  }
}

.crab-hero {
  margin-bottom: 24px;
  display: initial;
  @media screen and (max-width: 1024px) {
    display: none;
  }
}

.crab-hero-m {
  display: none;
  @media screen and (max-width: 1024px) {
    margin-bottom: 24px;
    display: initial;
  }
}

.main-crab-hero-btn-group {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 10px;
}
